@import url(https://fonts.googleapis.com/css?family=Dancing+Script:700);
.Dialog {
    --width: 50vw;

    position: absolute;
    top: 50%;
    left: 50%;
    width: var(--width);
    max-height: 70vh;
    margin: -40vmin 0 0 calc(var(--width) * -0.5);
    background-color: rgba(1, 1, 1, 0.9);
    border-radius: 2vmin;
    color: var(--color-white);
    padding: 4vmin;
    text-align: center;
}

.Help {
    position: absolute;
    right: 4vmin;
    background-color: rgba(1, 1, 1, 0.9);
    border-radius: 2vmin;
    color: var(--color-white);
    padding: 2vmin;
    transition: top 1s ease-in-out;
}
.Help.visible {
    top: 4vmin;
}
.Help.hidden {
    top: -20%;
}

.Help .red {
    color: var(--color-red-light);
}

.Help .blue {
    color: var(--color-blue-light);
}

.Help .green {
    color: var(--color-green-light);
}

.Help .yellow {
    color: var(--color-yellow-light);
}

.Tile {
  position: absolute;
  border: 0.4vmin solid var(--color-black);
  border-radius: 999px;
  background-color: var(--color-white);
}

.Tile.red {
  background-color: var(--color-red-light);
}

.Tile.blue {
  background-color: var(--color-blue-light);
}

.Tile.green {
  background-color: var(--color-green-light);
}

.Tile.yellow {
  background-color: var(--color-yellow-light);
}

.Tile.up::after,
.Tile.right::after,
.Tile.down::after,
.Tile.left::after {
  content: '';
  position: absolute;
  background-color: var(--color-black);
}

.Tile.up::after,
.Tile.down::after {
  width: 0.4vmin;
  height: 30%; /* 30% is the tile margin */
}

.Tile.right::after,
.Tile.left::after {
  width: 30%; /* 30% is the tile margin */
  height: 0.4vmin;
}

.Tile.up::after {
  bottom: 103%; /* don't ask... */
  left: 50%;
  margin-left: -0.2vmin;
}

.Tile.right::after {
  left: 103%;
  top: 50%;
  margin-top: -0.2vmin;
}

.Tile.down::after {
  top: 103%;
  right: 50%;
  margin-right: -0.2vmin;
}

.Tile.left::after {
  right: 103%;
  bottom: 50%;
  margin-bottom: -0.2vmin;
}

.Pawn {
  --transition-time: 1s;
  --transition-function: ease-in-out;

  cursor: pointer;
  position: absolute;
  border-radius: 999px;
  transition: top var(--transition-time) var(--transition-function),
    left var(--transition-time) var(--transition-function);
  box-shadow: 0.8vmin 0.4vmin 3vmin var(--color-shadow);
}

.Pawn.red {
  background-color: var(--color-red);
}

.Pawn.blue {
  background-color: var(--color-blue);
}

.Pawn.green {
  background-color: var(--color-green);
}

.Pawn.yellow {
  background-color: var(--color-yellow);
}

.Pawn.disabled {
  cursor: not-allowed;
}

.Pawn::after {
  content: '';
  display: block;
  width: 100%;
  height: 100%;
  background: url(/karel-erger-je-niet/static/media/karel.2eee1e02.png) 45% 40% no-repeat;
  background-size: 70%;
}

.Pawn:not(.disabled)::after {
  -webkit-animation: wobble 800ms ease-out 5s alternate infinite;
          animation: wobble 800ms ease-out 5s alternate infinite;
}

@-webkit-keyframes wobble {
  0% {
    -webkit-transform: rotate(30deg);
            transform: rotate(30deg);
  }
  20% {
    -webkit-transform: rotate(-30deg);
            transform: rotate(-30deg);
  }
  40% {
    -webkit-transform: rotate(30deg);
            transform: rotate(30deg);
  }
  60%, 100% {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }
}

@keyframes wobble {
  0% {
    -webkit-transform: rotate(30deg);
            transform: rotate(30deg);
  }
  20% {
    -webkit-transform: rotate(-30deg);
            transform: rotate(-30deg);
  }
  40% {
    -webkit-transform: rotate(30deg);
            transform: rotate(30deg);
  }
  60%, 100% {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }
}

.Title {
  font-family: var(--font-stack-fancy);
  font-size: 6vmin;
}

.Title .word {
  position: absolute;
  text-align: center;
  width: 20%;
}

.Title .word:nth-child(1) {
  top: 25%;
  left: 15%;
}

.Title .word:nth-child(2) {
  top: 25%;
  right: 15%;
}

.Title .word:nth-child(3) {
  bottom: 27%;
  left: 15%;
}

.Title .word:nth-child(4) {
  bottom: 27%;
  right: 15%;
}

.Board {
  position: relative;
  width: 96vmin;
  height: 96vmin;
  margin: 2vmin 40% 2vmin auto;
  border: 0.6vmin solid var(--color-red);
  border-radius: 1vmin;
  background-color: var(--color-board);
  box-shadow: 0 0 0.5vmin var(--color-shadow);
  -webkit-transform: rotate(1deg);
          transform: rotate(1deg);
}

.Board::after {
  content: '';
  position: absolute;
  top: 0.8vmin;
  right: 0.8vmin;
  bottom: 0.8vmin;
  left: 0.8vmin;
  border: 0.2vmin solid var(--color-black);
  pointer-events: none;
}

.inner {
  position: absolute;
  top: 4%;
  right: 4%;
  bottom: 4%;
  left: 4%;
}

.Die {
    --transition-time: 500ms;
    --timing-function: ease-out;

    cursor: pointer;
    position: absolute;
    width: 6vmin;
    height: 6vmin;
    background: radial-gradient(circle, var(--color-white) 65%, #ccc 100%);
    border-radius: 0.8vmin;
    box-shadow: 0.8vmin 0.4vmin 3vmin var(--color-shadow);
    transition: top var(--transition-time) var(--timing-function),
        left var(--transition-time) var(--timing-function),
        -webkit-transform var(--transition-time) var(--timing-function);
    transition: top var(--transition-time) var(--timing-function),
        left var(--transition-time) var(--timing-function),
        transform var(--transition-time) var(--timing-function);
    transition: top var(--transition-time) var(--timing-function),
        left var(--transition-time) var(--timing-function),
        transform var(--transition-time) var(--timing-function),
        -webkit-transform var(--transition-time) var(--timing-function);
}

.Die.disabled {
    cursor: not-allowed;
}

.Die .dot {
    --size: 1.15vmin;

    position: absolute;
    width: var(--size);
    height: var(--size);
    background-color: var(--color-black);
    border-radius: 999px;
    margin: calc(var(--size) * -0.5) 0 0 calc(var(--size) * -0.5);
}

.Die.value-1 .dot.top-left,
.Die.value-1 .dot.top-right,
.Die.value-1 .dot.dot.center-left,
.Die.value-1 .dot.dot.center-right,
.Die.value-1 .dot.dot.bottom-left,
.Die.value-1 .dot.dot.bottom-right,
.Die.value-2 .dot.top-right,
.Die.value-2 .dot.dot.center-left,
.Die.value-2 .dot.dot.center,
.Die.value-2 .dot.dot.center-right,
.Die.value-2 .dot.dot.bottom-left,
.Die.value-3 .dot.top-left,
.Die.value-3 .dot.dot.center-left,
.Die.value-3 .dot.dot.center-right,
.Die.value-3 .dot.dot.bottom-right,
.Die.value-4 .dot.dot.center-left,
.Die.value-4 .dot.dot.center,
.Die.value-4 .dot.dot.center-right,
.Die.value-5 .dot.dot.center-left,
.Die.value-5 .dot.dot.center-right,
.Die.value-6 .dot.dot.center {
    display: none;
}

.Die .dot.top-left {
    top: 25%;
    left: 25%;
}
.Die .dot.top-right {
    top: 25%;
    left: 75%;
}
.Die .dot.center-left {
    top: 50%;
    left: 25%;
}
.Die .dot.center {
    top: 50%;
    left: 50%;
}
.Die .dot.center-right {
    top: 50%;
    left: 75%;
}
.Die .dot.bottom-left {
    top: 75%;
    left: 25%;
}
.Die .dot.bottom-right {
    top: 75%;
    left: 75%;
}

:root {
  --color-black: #111;
  --color-white: #fff;
  --color-board: #fff9ba;
  --color-red: #f33;
  --color-red-light: #FF9797;
  --color-blue: #07e;
  --color-blue-light: #77B2EE;
  --color-green: #2c4;
  --color-green-light: #76CC87;
  --color-yellow: #fd0;
  --color-yellow-light: #FFEB66;
  --color-shadow: rgba(1, 1, 1, 0.5);

  --font-stack-fancy: 'Dancing Script', cursive;
}

* {
  box-sizing: border-box;
}

html,
body {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background: url(/karel-erger-je-niet/static/media/table-top.2bc5d0a4.jpeg) center center no-repeat;
  background-size: cover;
  line-height: 1.5;
}

h1,
h2,
h3 {
  font-family: 'Dancing Script', cursive;
  font-family: var(--font-stack-fancy);
}

h1 {
  font-size: 8vmin;
  margin: 0 0 calc(3vmin * 1.5);
}

p {
  font-size: 3vmin;
  margin: 0 0 calc(3vmin * 1.5);
}

button {
  cursor: pointer;
  display: inline-block;
  border: none;
  border-bottom: 0.4vmin solid #77B2EE;
  border-bottom: 0.4vmin solid var(--color-blue-light);
  background-color: #07e;
  background-color: var(--color-blue);
  border-radius: 1vmin;
  padding: 1.5vmin 2.5vmin;
  font-size: 3vmin;
  font-family: sans-serif;
  text-align: center;
  outline: none;
  color: #fff;
  color: var(--color-white);
}
button:hover {
  background-color: #77B2EE;
  background-color: var(--color-blue-light);
  border-bottom-color: #fff;
  border-bottom-color: var(--color-white);
}
button:active {
  margin-top: 0.4vmin;
  border-bottom-width: 0;
}

